import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {Route, RouterModule} from '@angular/router';
import { BookingPaymentComponent } from './booking-payment.component';
import { BookingDetailComponent } from './booking-detail/booking-detail.component';
import { BookingInvoiceComponent } from './booking-invoice/booking-invoice.component';
import {SharingModule} from '../shared/shared.module';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormValidatorService} from '../../../service/form-validator/form-validator.service';

const paymentRoute: Route[] = [
  {
    path: '',
    component: BookingPaymentComponent
  }
];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    SharingModule,

    RouterModule.forChild(paymentRoute),
  ],
  declarations: [
    BookingPaymentComponent,
    BookingDetailComponent,
    BookingInvoiceComponent,
  ],
  providers: [
    FormValidatorService
  ]
})

export class BookingPaymentModule { }



