import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {IOrderDetail} from '../../../../model/main/order/order';
import {ICustomer} from '../../../../model/main/customer';

@Component({
  selector: 'app-booking-payment',
  templateUrl: './booking-payment.component.html',
  styleUrls: ['./booking-payment.component.sass']
})
export class BookingPaymentComponent implements OnInit {

  orderDetail: IOrderDetail;
  currentStep = 1;
  /**Handle behavior of next btn*/
  isFinishBtnDisable = true;
  private customerInfo: ICustomer;
  private alterRequestDetail: any;

  constructor(private _activateRoute: ActivatedRoute) { }

  ngOnInit() {
    this.orderDetail = JSON.parse(this._activateRoute.snapshot.params['0']);
  }

  recvCusFormValue(event) {
    this.isFinishBtnDisable = true;
    if (event.valid && event.dirty) {
      this.isFinishBtnDisable = false;
      if (event.values) {
        this.customerInfo = event.values;
      }
    }
  }

  recvAlterResquestValue(event) {
    this.alterRequestDetail = event;
  }

  /**@description:
   * When click finish. Create an order and also auto create a new Account for customer
   */
  onCompleteBookingClick() {
    this.orderDetail.note = this.convertRequestToText();
    this.orderDetail.customer = this.customerInfo;
    this.orderDetail.total = this.orderDetail.quantity * this.orderDetail.room.price;
    this.currentStep = 2;
  }

  convertRequestToText(): string {
    let note = '';
    if (this.alterRequestDetail) {
      Object.keys(this.alterRequestDetail).forEach(key => {
        if (this.alterRequestDetail[key]) {
          switch (key) {
            case 'arriveAt':
              note += 'Arrive at: ' + this.alterRequestDetail[key];
              break;
            case 'note':
              note += ', note: ' + this.alterRequestDetail[key];
              break;
            case 'isQuite':
              note += ', quite room: ' + (this.alterRequestDetail[key] ? 'yes' : 'no');
              break;
            case 'isRooftop':
              note += ', roof top room: ' + (this.alterRequestDetail[key] ? 'yes' : 'no');
              break;
            case 'isAirportPickUp':
              note += ', pick me at airport: ' + (this.alterRequestDetail[key] ? 'yes' : 'no');
              break;
            case 'isCityView':
              note += ', city view room: ' + (this.alterRequestDetail[key] ? 'yes' : 'no');
              break;
            case 'isNoSmoking':
              note += ', no smoking room: ' + (this.alterRequestDetail[key] ? 'yes' : 'no');
              break;
          }
        }
      });
    }
    return note;
  }

}



