import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {IOrderDetail} from '../../../../../model/main/order/order';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {FormValidatorService} from '../../../../service/form-validator/form-validator.service';

@Component({
  selector: 'app-booking-detail',
  templateUrl: './booking-detail.component.html',
  styleUrls: ['./booking-detail.component.sass']
})
export class BookingDetailComponent implements OnInit {

  @Input() orderDetail: IOrderDetail;
  cusForm: FormGroup;
  alterRequestForm: FormGroup;
  isTypeTimeCheck = false;
  @Output() cusFormValueChange = new EventEmitter();
  @Output() alterRequestFormValueChange = new EventEmitter();
  formErrorMsg = {
    'email': '',
    'fullName': '',
    'phone': ''
  };

  constructor(private _fb: FormBuilder) { }

  ngOnInit() {
    this.initFormData();
  }

  initFormData() {
    this.cusForm = this._fb.group({
      fullName: [null, [Validators.minLength(3), Validators.required]],
      email: [null, [FormValidatorService.emailValidator, Validators.required]],
      phone: [null, [FormValidatorService.phoneValidator, Validators.minLength(10), Validators.required]]
    });
    this.detectCusFormChange();

    this.alterRequestForm = this._fb.group({
      arriveAt: [null],
      isRooftop: [false],
      isAirportPickUp: [false],
      isNoSmoking: [false],
      isQuite: [false],
      isCityView: [false],
      note: [null]
    });
    this.detectAlterRequestFormChange();
  }

  detectCusFormChange() {
    this.cusForm.valueChanges.subscribe(values => {
      let formChange: any = {
        valid: this.cusForm.valid,
        dirty: this.cusForm.dirty
      };
      if (this.cusForm.valid && this.cusForm.dirty) {
        formChange = {
          ...formChange,
          values
        };
      }
      this.cusFormValueChange.emit(formChange);
      /**Get error message from FormValidatorService*/
      for (const field in this.formErrorMsg) {
        this.formErrorMsg[field] = '';
        const control = this.cusForm.get(field);
        if (control && control.dirty && !control.valid) {
          for (const key in control.errors) {
            this.formErrorMsg[field] = FormValidatorService.getValidatorErrorMessage(key);
          }
        }
      }
    });
  }

  detectAlterRequestFormChange() {
    this.alterRequestForm.valueChanges.subscribe(values => {
      if (values.arriveAt) {
        if (this.isTypeTimeCheck) {
          values.arriveAt = values.arriveAt + 'am';
        } else {
          values.arriveAt = values.arriveAt + 'pm';
        }
      }
      this.alterRequestFormValueChange.emit(values);
    });
  }

}



